body {
  color: $base-font-color;
  font-family: $base-font-family;
  font-size: $base-font-size;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: $base-line-height;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $base-font-family;
  line-height: 1.25em;
  margin: 0;
  text-rendering: optimizeLegibility; // Fix the character spacing for headings
}

h1 {
  font-size: $base-font-size * 2.00; // 16 * 2.25 = 36px
  &.title {
    margin: 0 0 1em;
  } 
}

h2 {
  font-size: $base-font-size * 1.75; // 16 * 2 = 32px
}

h3 {
  font-size: $base-font-size * 1.45; // 16 * 1.75 = 28px
}

h4 {
  font-size: $base-font-size * 1.25; // 16 * 1.5 = 24px
}

h5 {
  font-size: $base-font-size * 1.15; // 16 * 1.25 = 20px
}

h6 {
  font-size: $base-font-size;
}

p {
  font-weight: 500;
  margin: 0 0 1.5em;
}

.small {
  font-size: .75em;
}

a {
  @include transitions;
  color: $base-link-color;
  text-decoration: none;
  &:hover {
    color: $hover-link-color;
    text-decoration: underline;
  }
  &:active, &:focus {
    color: $hover-link-color;
    outline: none;
  }
}

hr {
  border-bottom: 1px solid $medium-gray;
  border-left: none;
  border-right: none;
  border-top: none;
  margin: $base-line-height 0;
}

img {
  display: block;
  height: auto;
  margin: 0;
  max-width: 100%;
  width: auto;
  &.left, &.float-left, &.right, &.float-right {
    margin: 0 0 1em;
  }
}

abbr, acronym {
  border-bottom: 1px dotted $base-border-color;
  cursor: help;
}

address {
  display: block;
  margin: 0 0 ($base-line-height / 2);
}

hgroup {
  margin-bottom: $base-line-height / 2;
}

del {
  color: lighten($base-font-color, 15);
}

blockquote {
  border-left: 2px solid $base-border-color;
  color: lighten($base-font-color, 15);
  margin: $base-line-height 0;
  padding-left: $base-line-height / 2;
}

cite {
  color: lighten($base-font-color, 25);
  font-style: italic;

  &:before {
    content: '\2014 \00A0';
  }
}

.float-left, .float-right {
  margin: 0 0 1.5em;
}


/* 480+ styles 
  --------------------------------------------------------------------- */
@media all and (min-width: 480px) {}


/* 768+ styles 
  --------------------------------------------------------------------- */
@media all and (min-width: 768px) {
  
  
  img {
    &.left, &.float-left {
      float: left;
      margin: .25em 2em 2em 0;
    }
    &.right, &.float-right {
      float: right;
      margin: .25em 0 2em 2em;
    }
  }
  
  .float-left {
    float: left;
    margin: .25em 2em 2em 0;
  }
  .float-right {
    float: right;
    margin: .25em 0 2em 2em;
  }
  
  .width-25 {
    width: 25%;
  }
  .width-33 {
    width: 33%;
  }
  .width-50 {
    width: 50%;
  }
  .width-66 {
    width: 66%;
  }
  .width-75 {
    width: 75%;
  }
  
}


/* 960+ styles 
  --------------------------------------------------------------------- */
@media all and (min-width: 960px) {}


/* 1200+ styles 
  --------------------------------------------------------------------- */
@media all and (min-width: 1200px) {}